.about_section-dark {
  margin: 5rem 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.about_text-dark {
  justify-content: space-around;
  width: 80%;
  flex-wrap: wrap;
  background-color: lighten(#282c34, 4);
  border-radius: 1rem;
  padding: 2rem;
  color: lighten(#282c34, 50);
  font-size: 2rem;
  line-height: 2.6rem;
}
.about_title-dark {
  width: 100%;
  font-size: 5rem;
  text-align: center;
  color: #0077b5;
  font-weight: 400;
}
//light
.about_section-light {
  margin: 5rem 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.about_text-light {
  justify-content: space-around;
  width: 80%;
  flex-wrap: wrap;
  background-color: white;
  border-radius: 1rem;
  padding: 2rem;
  color: #282c34;
  font-size: 2rem;
  line-height: 2.6rem;
  box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25),
    0 0.7rem 0.7rem rgba(0, 0, 0, 0.22);
}
.about_title-light {
  width: 100%;
  font-size: 5rem;
  text-align: center;
  color: white;
  text-shadow: 1px 1px 2px rgb(70, 70, 70);
  font-weight: 400;
}
